import Vue from "vue";
import VueRouter, { RouteConfig } from "vue-router";

Vue.use(VueRouter);

const routes: Array<RouteConfig> = [
  {
    path: "/",
    name: "Home",
    component: () =>
      import(
        /* webpackChunkName: "blogs" */ "../modules/home/pages/Home.page.vue"
      )
  },
  {
    path: "/blogs",
    name: "Blogs",
    component: () =>
      import(
        /* webpackChunkName: "blogs" */ "../modules/blogs/pages/Blogs.page.vue"
      ),
    meta: {
      navOverlap: true,
      layout: "side-by-side"
    }
  },
  {
    path: "/gafu",
    name: "Gafu",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(
        /* webpackChunkName: "gafu" */ "../modules/gafu/pages/Gafu.page.vue"
      )
  }
];

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes
});

export default router;
