<template>
  <div id="app" :class="layout">
    <TopNavBar></TopNavBar>
    <transition
      name="fade"
      mode="out-in"
      @leave="leave"
      @before-enter="beforeEnter"
    >
      <router-view />
    </transition>
  </div>
</template>
<script>
import TopNavBar from "@/modules/@layout/components/TopNavBar";

export default {
  components: {
    TopNavBar
  },
  computed: {
    layout() {
      if (!this.transitionDone) {
        return this.lastLayout;
      }
      switch (this.$route.meta.layout) {
        case "side-by-side":
          return this.$route.meta.layout;
        default:
          return "";
      }
    }
  },
  data() {
    return {
      transitionDone: true,
      lastLayout: ""
    };
  },
  methods: {
    beforeEnter() {
      this.transitionDone = true;
    },
    leave(el, done) {
      done();
      this.transitionDone = true;
      this.lastLayout = this.$route.meta.layout;
    }
  }
};
</script>

<style lang="scss"></style>
